import React from "react";
import RateBar from "./Rater";

const List = ({ answrs, revealer }) => {
  return answrs.map((questbox) => {                 //question
    const { id, rating, name, job, image, text} = questbox;
    return (
      <article key={id} className="list">
        <img className="pfp" src={image} alt="sorry, pal" />
    
        <div style={{ position: "relative" }}>
          <p>{name}</p>
          <p>{job}</p>
            <p className="answer">{text}</p>
        </div>
      </article>
    );
  });
};

export default List;
